import Vue from 'vue'
import Vuex from 'vuex'
import router from '@/router'

Vue.use(Vuex);

const userStore = {
    state: {
        id: '',
        name: '',
        token: ''
    },
    mutations: {
        login: function (state, payload) {
            state.id = payload.id
            state.name = payload.name
            state.token = payload.token
        },
        loginCheck: function (state) {
            if (!state.token) {
                router.push({
                    name: 'login'
                }).catch(error => {
                    console.log(error)
                })
            }
        }

    }
}

export default userStore